import styled from 'styled-components';
import React from 'react';
import DesktopToolbar from './Toolbar';
import duck from '../assets/duck.svg';
import cursor from '../assets/cursor.svg';

const Page = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
`;

const Desktop = styled.div`
    display: block;
    width: 1280px;
    height: 720px;
    background-color: #111010;
    cursor: url(${cursor}) , auto;
`;

const Wallpaper = styled.div`
    display: flex;
    position: relative;
    height: 94%;
`;

const DuckImage = styled.img`
    width: 142px;
    height: 164px;
`;

const LogoText = styled.p`
    align-self: flex-end;
    color: #8D8080;
`;

const Logo = styled.div`
    font-size: 22pt;
    display: flex;
    position: absolute;
    bottom: 0;
    padding-left: 0%;
`;


const SystemLogo: React.FC = () => <Logo>
    <DuckImage src={duck} title='Quack' alt='Mr Duck' />
    <LogoText>QUACKOS 0.3</LogoText>
</Logo>

const BasePage: React.FC = ({ children }) => {
    return (
        <Page>
            <Desktop>
                <DesktopToolbar />
                <Wallpaper>
                    <SystemLogo />
                    {children}
                </Wallpaper>
            </Desktop >
        </Page>
    )
}

export default BasePage;
